import {
  LeftModalHeader,
  ScreenWrapper,
  UserAvatar,
  RecycleList, DynamicHeader,
} from '@true/ui';
import React, {memo, useCallback, useMemo} from 'react';
import {translate} from '@true/core';
import {styled} from '@true/core';
import {Button} from '@policy/components/Button';
import {Fonts, IC_SEARCH} from '@true/core';
import {IC_APARTMENT, IC_FOLDER_TEXT, IC_SAVED, IC_SETTING} from '@policy/assets';
import {StyleSheet, View} from 'react-native';
import {useMe, useUserGroup} from '@true/core';
import {useStorages, useStoragesByQuery} from '@policy/store/storages';
import {useUserGroupByQuery} from '@true/core';
import {stripSpecialCharacters} from '@true/core';
import {navigateToOneTabPolicyScreen, navigateToSearchScreen} from '@policy/utils';
import {
  requestPolicyBySaved,
  requestPolicyBySender,
  requestPolicyByStorage,
} from '@policy/store/policies/functions';

interface IItemData {
  id: string | number;
  type: 'sender' | 'saved' | 'storage';
}

interface ISectionData {
  title: string;
  id: number;
  icon: any;
}

export const MenuScreen = memo(() => {
  const me = useMe();

  const renderItem = useCallback(
    (data: IItemData, index?: number) => <Item data={data} index={index} />,
    [],
  );

  const renderSection = useCallback(
    (data: ISectionData, onToggle?: () => void, index?: number) => (
      <Section data={data} index={index} />
    ),
    [],
  );

  const storages = useStoragesByQuery('all');
  const senders = useUserGroupByQuery('teams');

  const data: ISectionData[] = useMemo(
    () => [
      {
        id: 0,
        title: translate('policy.storages'),
        // data: storages.map(id => ({id, type: 'storage'})),
        icon: IC_FOLDER_TEXT,
      },
      {
        id: 1,
        title: translate('policy.senders'),
        // data: senders.map(id => ({id, type: 'sender'})),
        icon: IC_APARTMENT,
      },
      {
        id: 2,
        title: translate('policy.saved'),
        // data: ['expired', 'rejected', 'archived', 'trash'].map(id => ({
        //   id,
        //   type: 'saved',
        // })),
        icon: IC_SAVED,
      },
    ],
    [storages, senders],
  );

  const map: Record<string, IItemData[]> = useMemo(
    () => ({
      '0': storages.map(id => ({id, type: 'storage'})),
      '1': senders.map(id => ({id, type: 'sender'})),
      '2': ['expired', 'rejected', 'archived', 'trash'].map(id => ({
        id,
        type: 'saved',
      })),
    }),
    [storages, senders],
  );

  // console.log(data);

  return (
    <ScreenWrapper isGrey={true}>
      <LeftModalHeader
        hideSeparator={true}
        title={translate('policy.menu')}
        rightComponent={
          <SRow>
            <Button source={IC_SEARCH} onPress={navigateToSearchScreen} />
            <Button source={IC_SETTING} style={styles.headerIcon} />
          </SRow>
        }
      />
      <SUserInfoWrapper>
        <UserAvatar userKey={me?.id || ''} size={40} />
        <SUserText>{me?.name}</SUserText>
      </SUserInfoWrapper>
      <RecycleList
        data={data}
        mapKey={'id'}
        map={map}
        itemHeight={50}
        renderItem={renderItem}
        renderSection={renderSection}
        loading={false}
        isRefreshing={false}
        isLoadMore={false}
        error={false}
        refreshable={false}
        loadMorable={false}
      />
    </ScreenWrapper>
  );
});

const styles = StyleSheet.create({
  headerIcon: {
    marginLeft: 16,
  },
});

const SRow = styled.View`
  flex-direction: row;
  margin-right: 16px;
`;

const SUserInfoWrapper = styled.View`
  flex-direction: row;
  padding: 8px 16px;
  align-items: center;
  background-color: ${p => p.theme.backgroundColor};
  border-bottom-width: 1px;
  border-color: ${p => p.theme.grey5};
`;
const SUserText = styled.Text`
  font-size: 20px;
  line-height: 28px;
  font-family: ${Fonts.Bold};
  margin-left: 8px;
  color: ${p => p.theme.grey1};
`;

const Section = memo(
  ({data, index}: {data: {title: string; icon: any}; index?: number}) => {
    return (
      <SSectionContainer>
        <SSectionIcon source={data.icon} />
        <SSectionText>{data.title}</SSectionText>
      </SSectionContainer>
    );
  },
);
const Item = memo(({data, index}: {data: IItemData; index?: number}) => {
  const render = useCallback(
    (text: string, onPress?: () => void) => {
      return (
        <View>
          <SItemContainer onPress={onPress}>
            <SItemText>{text}</SItemText>
          </SItemContainer>
          <SDividerView>
            <SItemDivider />
          </SDividerView>
        </View>
      );
    },
    [index],
  );

  if (data.type === 'sender') {
    const sender = useUserGroup(data.id);

    return render(stripSpecialCharacters(sender?.name), () => {
      navigateToOneTabPolicyScreen({
        params: {id: data.id},
        query: 'sender',
        requestFn: requestPolicyBySender,
        title: sender?.name || '',
        items: ['collection', 'storage', 'receiver', 'doctype'],
      });
    });
  }

  if (data.type === 'storage') {
    const storage = useStorages(data.id);

    return render(stripSpecialCharacters(storage?.name), () => {
      navigateToOneTabPolicyScreen({
        params: {id: data.id},
        query: 'storage',
        requestFn: requestPolicyByStorage,
        title: storage?.name || '',
        items: ['collection', 'doctype'],
      });
    });
  }

  return render(translate('policy.' + data.id.toString()), () => {
    navigateToOneTabPolicyScreen({
      params: {tail: data.id},
      query: 'saved',
      requestFn: requestPolicyBySaved,
      title: translate('policy.' + data.id.toString()),
      items: ['collection', 'storage', 'receiver', 'doctype'],
      mainKey: 'tail',
    });
  });
});

const SSectionContainer = styled.View`
  flex-direction: row;
  align-items: center;
  padding: 12px 16px;
  background-color: ${p => p.theme.backgroundColor};
  margin-top: 8px;
`;
const SSectionText = styled.Text`
  color: ${p => p.theme.grey1};
`;
const SSectionIcon = styled.Image`
  margin-right: 8px;
  height: 24px;
  width: 24px;
`;

const SItemContainer = styled.TouchableOpacity`
  padding: 16px;
  background-color: ${p => p.theme.backgroundColor};
`;
const SItemText = styled.Text`
  color: ${p => p.theme.grey1};
`;
const SItemDivider = styled.View`
  height: 1px;
  background-color: ${p => p.theme.grey5};
`;
const SDividerView = styled.View`
  background-color: ${p => p.theme.backgroundColor};
  padding: 0 16px;
`;
