import {styled} from '@true/core';
import {memo} from 'react';
import {ICollection, IFolder} from '@policy/store/collections/types';
import {Text} from 'react-native';

export const CollectionItem = memo(
  ({item, index}: {index?: number; item: IFolder}) => {
    return (
      <SContainer>
        <SText>{item.name}</SText>
      </SContainer>
    );
  },
);

const SContainer = styled.View`
  padding: 12px 16px;
  border-left-width: 1px;
  border-right-width: 1px;
  border-bottom-width: 1px;
  border-color: ${p => p.theme.grey5};
  background-color: ${p => p.theme.backgroundColor};
`;
const SText = styled.Text`
  color: ${p => p.theme.grey1};
`;
