import {DynamicHeader, LeftModalHeader, ScreenWrapper} from '@true/ui';
import React, {memo, useState} from 'react';
import {translate} from '@true/core';
import {DEFAULT_PARAMS, IParams} from '@policy/screens/PersonalScreen/index';
import {useNavigationParams} from '@true/core';
import {FilterComp} from '@policy/components/FilterComp';

export interface PersonalFilterProps {
  params: IParams;
  setParams: any;
}

export const PersonalFilter = memo(() => {
  const {params, setParams} = useNavigationParams<PersonalFilterProps>();
  const [_params, _setParams] = useState(params);

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.advance_filter')} />
      <FilterComp
        items={[
          'collection',
          'folder',
          'storage',
          'receiver',
          'doctype',
          'created_date',
          'effective_date',
          'expired_date',
        ]}
        params={_params}
        setParams={_setParams}
        defaultParams={DEFAULT_PARAMS}
        onSubmit={setParams}
      />
    </ScreenWrapper>
  );
});
