import React, {memo, useCallback, useMemo} from 'react';
import {styled} from '@true/core';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {UserAvatar} from '@policy/components';
import {getUser, useUser} from '@true/core';
import {
  IC_APPROVED_STATUS,
  IC_PENDING_STATUS,
  IC_REJECTED_STATUS,
} from '@policy/assets';
import {Fonts} from '@true/core';
import {Colors} from '@true/core';
import {useBoolean} from '@true/core';
import {ProfileLightBox} from '@true/ui';
import {EReview, IApprovalData} from '@policy/screens/PolicyDetailScreen/index';
import { FlatList } from "react-native";

export const DocumentReview = memo(
  (props: {policy?: IDetailPolicy; approvableData?: IApprovalData}) => {
    return (
      <SContainer>
        <FlatList data={props.policy?.owners} renderItem={({item, index}) => {
          return <ReviewItem
            index={index}
            approvableData={props.approvableData}
            owner={item as any}
          />
        }}/>
      </SContainer>
    );
  },
);

const SContainer = styled.View`
  padding-bottom: 8px;
`;

const ReviewItem = memo(
  ({
    index,
    owner,
    approvableData,
  }: {
    index: number;
    approvableData?: IApprovalData;
    owner: {
      username: string;
      id: number;
      status: 0 | '-1' | '1';
      note: string;
    };
  }) => {
    const [visible, show, hide] = useBoolean();

    const status = useMemo(() => {
      if (owner?.status == 0)
        return {
          text: 'Pending',
          source: IC_PENDING_STATUS,
          color: Colors.yellow1,
        };
      if (owner?.status == '1')
        return {
          text: 'Approved',
          source: IC_APPROVED_STATUS,
          color: Colors.green1,
        };
      return {text: 'Rejected', source: IC_REJECTED_STATUS, color: Colors.red1};
    }, [owner?.status]);

    // return opacity = 0.4 if policy is sequence approval and index of user is higher than index of current approver
    const opacity = useMemo(() => {
      if (
        approvableData?.currentIndex !== undefined &&
        index > approvableData.currentIndex + 1
      )
        return 0.4;
      return 1;
    }, [approvableData?.currentIndex, index]);

    const user = useUser(owner.id);

    return (
      <SReviewContainer style={{opacity}} onLongPress={show}>
        <SUSerWrapper>
          <UserAvatar userKey={owner.id} size={40} />
          <STextWrapper>
            <SUserName>{user?.name}</SUserName>
            {user?.title ? <SSubTitle>{user?.title}</SSubTitle> : null}
          </STextWrapper>
        </SUSerWrapper>
        <SStatusWrapper>
          <StatusText style={{color: status.color}}>{status.text}</StatusText>
          <SStatusIc source={status.source} />
        </SStatusWrapper>

        <ProfileLightBox
          isVisible={visible}
          userKey={owner.id}
          onCloseRequest={hide}
        />
      </SReviewContainer>
    );
  },
);

const SReviewContainer = styled.TouchableOpacity`
  flex-direction: row;
  min-height: 40px;
  margin: 8px 0;
`;

const SUSerWrapper = styled.View`
  flex-direction: row;
  align-items: center;
  flex: 1;
`;
const STextWrapper = styled.View`
  margin-left: 8px;
`;
const SStatusWrapper = styled.View`
  flex-direction: row;
  align-items: center;
`;
const SUserName = styled.Text`
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Medium};
  color: ${p => p.theme.grey1};
`;
const SSubTitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.grey2};
`;
const StatusText = styled.Text`
  margin-right: 6px;
  font-size: 14px;
  line-height: 16px;
  font-family: ${Fonts.Medium};
`;
const SStatusIc = styled.Image``;
