import React, {memo, useState} from 'react';
import {useNavigationParams} from '@true/core';
import {DynamicHeader, ScreenWrapper} from '@true/ui';
import {translate} from '@true/core';
import {FilterComp, IInputFilterType} from '@policy/components/FilterComp';

export interface DynamicFilterScreenProps {
  params: any;
  setParams: (value: any) => void;
  items: IInputFilterType[];
  mainKey?: string;
}

export const DynamicFilterScreen = memo(() => {
  const {
    params,
    setParams,
    items,
    mainKey = 'id',
  } = useNavigationParams<DynamicFilterScreenProps>();

  const [_params, _setParams] = useState(params);

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.filter')} />
      <FilterComp
        items={items}
        params={_params}
        setParams={_setParams}
        defaultParams={{[mainKey]: params?.[mainKey]}}
        onSubmit={setParams}
      />
    </ScreenWrapper>
  );
});
