import {Fonts, IC_ARROW, IC_MORE, IC_X} from '@true/core';
import {styled, translate, useTheme} from '@true/core';
import React, {memo, useCallback, useRef} from 'react';
import Animated, {
  SharedValue,
  useAnimatedProps,
  useAnimatedStyle,
  useDerivedValue,
} from 'react-native-reanimated';
import {StyleSheet, View, ViewProps} from 'react-native';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import {useSelectedItem} from '@policy/context/SelectedItemContext';
import {getHeight, getWidth} from '@true/core';
import {ReText} from 'react-native-redash';
import {
  ConfirmationDialog,
  LeftModalHeader,
  MyBottomSheet,
} from '@true/ui';
import {HomeMoreMenu} from '@policy/screens/HomeScreen/HomeMoreMenu';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {Button} from '@policy/components/Button';
import {IC_OUTLINED_FLAG, IC_MARK_READ, IC_REMOVE_BOOKMARK} from '@policy/assets';
import {requestMarkRead} from '@policy/store/policies/functions';
import {useAsyncFn} from 'react-use';
import {useBoolean} from '@true/core';
import {Colors} from '@true/core';

export const SelectedHeader = memo(
  ({
    title,
    animatedHeight,
    ...rest
  }: {title: string; animatedHeight?: SharedValue<number>} & ViewProps) => {
    const bottomSheetRef = useRef<BottomSheetModal>(null);

    const [visible, show, hide] = useBoolean();
    const theme = useTheme();

    const {aTop} = useAnimatedSafeAreaInsets();
    const {selectedIds, setSelectedState, selectedState} = useSelectedItem();

    const animatedProps = useAnimatedProps(
      () =>
        ({
          pointerEvents: selectedState ? 'auto' : 'none',
        } as any),
    );

    const animatedText = useDerivedValue(() =>
      selectedIds.value.length ? ` (${selectedIds.value.length})` : '',
    );
    const animatedHeaderStyle = useAnimatedStyle(() => {
      return {
        height: (animatedHeight?.value || 0) + aTop.value + 8,
      };
    });

    const onCloseHeader = useCallback(() => {
      selectedIds.value = [];
      setSelectedState(false);
    }, []);

    const [{loading}, onReadMark] = useAsyncFn(async () => {
      await requestMarkRead({
        'ids[]': selectedIds.value,
      });
      onCloseHeader();
    }, [selectedIds]);

    return (
      <SContainer
        {...rest}
        animatedProps={animatedProps}
        style={[
          rest.style,
          {opacity: selectedState ? 1 : 0},
          animatedHeaderStyle,
        ]}>
        <LeftModalHeader
          hideSeparator={true}
          title={title}
          rightComponent={
            <STextWrapper>
              <SText>{translate('policy.selected')}</SText>
              <SAnimatedText text={animatedText} />
            </STextWrapper>
          }
        />
        <View style={{flex: 1}} />
        <SActionWrapper>
          <SButton source={IC_X} onPress={onCloseHeader} />
          <View style={{flex: 1}} />
          {/*<SButton source={IC_REMOVE_BOOKMARK} />*/}
          {/*<SButton style={styles.leftButton} source={IC_OUTLINED_FLAG} />*/}
          <SButton
            style={styles.leftButton}
            source={IC_MARK_READ}
            onPress={show}
            hideContentWhileLoading={true}
          />
        </SActionWrapper>
        {/*<HeaderIconWrapper*/}
        {/*  onPress={() => {*/}
        {/*    selectedIds.value = [];*/}
        {/*    setSelectedState(false);*/}
        {/*  }}>*/}
        {/*  <SIcon source={IC_ARROW} />*/}
        {/*</HeaderIconWrapper>*/}
        {/*<STextWrapper>*/}
        {/*  <SText>{translate('policy.selected')}</SText>*/}
        {/*  <SAnimatedText text={animatedText} />*/}
        {/*</STextWrapper>*/}

        {/*<View style={{flex: 1}} />*/}
        {/*<RightActions>*/}
        {/*  <SRightIconBtn onPress={() => bottomSheetRef.current?.present()}>*/}
        {/*    <SIcon source={IC_MORE} />*/}
        {/*  </SRightIconBtn>*/}
        {/*</RightActions>*/}

        {/*<HomeMoreMenu ref={bottomSheetRef} />*/}
        <ConfirmationDialog
          title={translate('policy.mark_read')}
          content={translate('policy.mark_read_content')}
          isVisible={visible}
          onCloseRequest={hide}
          buttons={[
            {text: translate('policy.cancel')},
            {
              text: translate('policy.confirm'),
              onPress: onReadMark,
              loading: loading,
              backgroundColor: theme.primaryColor,
              color: Colors.white,
            },
          ]}
        />
      </SContainer>
    );
  },
);

const SContainer = styled(Animated.View)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: ${p => p.theme.backgroundColor};
  z-index: 99;
  border-bottom-width: 1px;
  border-color: ${p => p.theme.grey4};
`;
const SRightIconBtn = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    left: 8,
    right: 8,
    bottom: 8,
  },
}))`
  margin-right: 16px;
`;
const SIcon = styled.Image``;

const STextWrapper = styled.View`
  align-items: center;
  flex-direction: row;
  margin-right: 16px;
`;

const SAnimatedText = styled(ReText)`
  font-size: 14px;
  line-height: 16px;
  color: ${p => p.theme.grey2};
`;

const SText = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.grey2};
`;

const HeaderIconWrapper = styled.TouchableOpacity`
  min-width: ${getHeight(40)}px;
  height: ${getHeight(40)}px;
  justify-content: center;
  align-items: center;
  padding-left: ${getWidth(16)}px;
  padding-right: 16px;
  margin-right: -16px;
`;

const RightActions = styled.View`
  width: ${getWidth(70)}px;
  max-height: ${getHeight(48)}px;
  justify-content: center;
  align-items: flex-end;
`;

const SActionWrapper = styled.View`
  flex-direction: row;
  padding: 12px 16px;
  background-color: ${p => p.theme.backgroundColor};
`;

const SButton = styled(Button).attrs(p => ({
  imageStyle: {
    tintColor: p.theme.grey1,
  },
}))``;

const styles = StyleSheet.create({
  leftButton: {
    marginLeft: 24,
  },
});
