import React, {memo} from 'react';
import {IC_FILTER} from '@true/core';
import {getCountFilter} from '@policy/utils/filter';
import {styled} from '@true/core';
import {Colors} from '@true/core';
import {TouchableOpacityProps} from 'react-native';

export const FilterIcon = memo(
  ({params, ...rest}: {params: any} & TouchableOpacityProps) => {
    return (
      // @ts-ignore
      <SBackBtn {...rest}>
        <SBackIc source={IC_FILTER} />
        <CountFilterView count={getCountFilter(params)} />
      </SBackBtn>
    );
  },
);

const SBackBtn = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    bottom: 8,
    left: 8,
    right: 8,
  },
}))``;

const SBackIc = styled.Image``;

const CountFilterView = memo(({count}: {count: number}) => {
  if (!count) return null;
  return (
    <SCountContainer>
      <SCountText>{count}</SCountText>
    </SCountContainer>
  );
});

const SCountContainer = styled.View`
  position: absolute;
  border-radius: 10px;
  width: 16px;
  height: 16px;
  align-items: center;
  justify-content: center;
  background-color: red;
  top: -5px;
  right: -5px;
`;
const SCountText = styled.Text`
  color: ${Colors.white};
  font-size: 10px;
  line-height: 16px;
`;
