import React, { memo } from "react";
import { addLangResources } from "@true/core";
import { MiniAppProvider } from "@true/ui";
import { Modal } from "@policy/Routes";
import { persistor } from "@policy/store";
import { requestDoctypes } from "@policy/store/doctypes/functions";
import { requestStorages } from "@policy/store/storages/functions";
import { requestOffices } from "@policy/store/offices/functions";
import { requestCollections } from "@policy/store/collections/functions";
import { requestLoadFavDocs } from "@policy/store/favDocs/functions";

/* -- add languages -- */
addLangResources({
  en: { ...require("@policy/assets/languages/en.json") },
  vi: { ...require("@policy/assets/languages/vi.json") },
}).then()

/* -- sync init data -- */
const syncData = async () => {
  await requestDoctypes();
  await requestStorages();
  await requestOffices();
  await requestCollections();
  await requestLoadFavDocs();
}

const MiniApp = memo(() => {

  return <MiniAppProvider
    persistor={persistor}
    syncData={syncData}
    primaryColor={"#E1B12C"}>
      <Modal/>
  </MiniAppProvider>
})

export default MiniApp;
