import React, {memo, useCallback, useState} from 'react';
import {
  EmptyHeader,
  ScreenWrapper,
  ScrollableTabView,
  SearchBar,
} from '@true/ui';
import {IC_ARROW} from '@true/core';
import {styled, translate} from '@true/core';
import {goBack} from '@true/core';
import {navigateToSearchFilter} from '@policy/utils';
import {
  IRequestSearchPolicyParams,
  requestSearchPolicy,
} from '@policy/store/policies/functions';
import {getPoliciesByQuery, usePoliciesByQuery} from '@policy/store/policies';
import {PoliciesItem} from '@policy/screens/HomeScreen/PoliciesItem';
import {FilterIcon} from '@policy/components/FilterIcon';
import {
  SelectedItemProvider,
  useSelectedItem,
} from '@policy/context/SelectedItemContext';
import {SelectedHeader} from '@policy/screens/HomeScreen/SelectedHeader';
import {useDerivedValue} from 'react-native-reanimated';

export interface SearchScreenProps {}

export const defaultParams: IRequestSearchPolicyParams = {
  storage: '',
  receiver: '',
  collection: '',
  folder: '',
  doctype: '',
  created_date_from: null,
  created_date_to: null,
  effective_date_from: null,
  effective_date_to: null,
  expired_date_from: null,
  expired_date_to: null,
  q: '',
  created_by: null,
};

const _SearchScreen = memo(() => {
  const [params, setParams] = useState(defaultParams);

  const {selectedState, selectedIds} = useSelectedItem();

  const scrollEnable = useDerivedValue(() => selectedIds.value.length == 0);

  const renderItem = useCallback(
    (policyId: string | number, index?: number, isLast?: boolean) => {
      return <PoliciesItem id={policyId as number} isLast={isLast} />;
    },
    [],
  );

  return (
    <ScreenWrapper isGrey={true}>
      <EmptyHeader>
        <SRow>
          <SBackBtn onPress={goBack}>
            <SBackIc source={IC_ARROW} />
          </SBackBtn>
          <SView>
            <SearchBar
              containerStyle={{
                height: 40,
                marginTop: 0,
                marginBottom: 0,
              }}
              onSearchTextChange={e => {
                setParams(prev => ({
                  ...prev,
                  q: e,
                }));
              }}
            />
          </SView>
          <FilterIcon
            params={params}
            onPress={() =>
              navigateToSearchFilter({
                params,
                setParams,
              })
            }
          />
        </SRow>
      </EmptyHeader>
      <SelectedHeader title={translate('policy.search')} />
      <ScrollableTabView.Provider
        disabledLoadData={selectedState}
        scrollEnabled={scrollEnable}
        params={params}
        requestFn={requestSearchPolicy}
        query={'search'}
        getItemByQuery={getPoliciesByQuery}
        useItemByQuery={usePoliciesByQuery}
        renderItem={renderItem}
        tabs={[
          {
            title: translate('policy.sent_to_me'),
            content: p => (
              <ScrollableTabView.List index={0} tab={'documents'} />
            ),
          },
          {
            title: translate('policy.followings'),
            content: p => (
              <ScrollableTabView.List index={1} tab={'following'} />
            ),
          },
          {
            title: translate('policy.created_by_me'),
            content: p => <ScrollableTabView.List index={2} tab={'sent'} />,
          },
        ]}>
        <ScrollableTabView.TabBar />
        <ScrollableTabView.Content />
      </ScrollableTabView.Provider>
    </ScreenWrapper>
  );
});

export const SearchScreen = memo(() => {
  return (
    <SelectedItemProvider>
      <_SearchScreen />
    </SelectedItemProvider>
  );
});

const SRow = styled.View`
  flex-direction: row;
  padding: 0 16px;
  align-items: center;
`;
const SBackBtn = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    bottom: 8,
    left: 8,
    right: 8,
  },
}))``;

const SBackIc = styled.Image``;
const SView = styled.View`
  flex: 1;
`;
