import React, {memo} from 'react';
import {styled, translate} from '@true/core';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {IC_FOLDER_STAR, IC_POLICY_DOCUMENT} from '@policy/assets';
import {Fonts} from '@true/core';
import {useDoctypes} from '@policy/store/doctypes';

export const DetailDoctype = memo(({policy}: {policy?: IDetailPolicy}) => {
  // console.log(policy?.mv_export);
  const doctype = useDoctypes(policy?.doctype_id);

  return (
    <SContainer>
      <SHeaderWrapper>
        <SHeaderIconWrapper>
          <SHeaderIcon source={IC_POLICY_DOCUMENT} />
        </SHeaderIconWrapper>
        <SHeaderTextWrapper>
          <SHeaderText>{doctype?.name}</SHeaderText>
          <SHeaderSubText>{translate('policy.custom_field')}</SHeaderSubText>
        </SHeaderTextWrapper>
      </SHeaderWrapper>
    </SContainer>
  );
});

const SContainer = styled.View`
  padding: 8px 0 12px;
`;
const SHeaderWrapper = styled.View`
  flex-direction: row;
`;
const SHeaderIconWrapper = styled.View`
  margin-right: 8px;
  justify-content: center;
  max-height: 44px;
`;
const SHeaderIcon = styled.Image``;
const SHeaderTextWrapper = styled.View``;
const SHeaderText = styled.Text`
  font-size: 16px;
  line-height: 24px;
  font-family: ${Fonts.Medium};
  color: ${p => p.theme.grey1};
`;
const SHeaderSubText = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.grey2};
`;
