import {styled, translate} from '@true/core';
import {
  DynamicHeader,
  LeftModalHeader,
  ScreenWrapper,
  ScrollableTabView,
} from '@true/ui';
import React, {memo, useCallback, useMemo, useState} from 'react';
import {
  requestPolicyByMine,
  requestPolicyByMineFav,
} from '@policy/store/policies/functions';
import {PoliciesItem} from '@policy/screens/HomeScreen/PoliciesItem';
import {getPoliciesByQuery, usePoliciesByQuery} from '@policy/store/policies';
import {Button} from '@policy/components/Button';
import {Fonts, IC_SEARCH} from '@true/core';
import {navigateToPersonalFilter, navigateToSearchScreen} from '@policy/utils';
import {getCountFilter} from '@policy/utils/filter';
import {Colors} from '@true/core';
import {
  SelectedItemProvider,
  useSelectedItem,
} from '@policy/context/SelectedItemContext';
import {SelectedHeader} from '@policy/screens/HomeScreen/SelectedHeader';
import {useDerivedValue} from 'react-native-reanimated';

export interface IParams {
  collection: string | number;
  storage: string | number;
  doctype: string | number;
  filter: string | number;
  sort: string | number;
  created_date_from: string | number;
  created_date_to: string | number;
  effective_date_from: string | number;
  effective_date_to: string | number;
  expired_date_from: string | number;
  expired_date_to: string | number;
}

export const DEFAULT_PARAMS = {
  collection: '',
  storage: '',
  doctype: '',
  filter: '',
  sort: '',
  created_date_from: '',
  created_date_to: '',
  effective_date_from: '',
  effective_date_to: '',
  expired_date_from: '',
  expired_date_to: '',
};

export const PersonalScreen = memo(() => {
  const [params, setParams] = useState(DEFAULT_PARAMS);

  const renderItem = useCallback(
    (id: number, index?: number, isLast?: boolean) => {
      return <PoliciesItem id={id} index={index} isLast={isLast} />;
    },
    [],
  );

  const onFilterPress = useCallback(() => {
    navigateToPersonalFilter({
      params,
      setParams,
    });
  }, [params]);

  return (
    <SelectedItemProvider>
      <ScreenWrapper isGrey={true}>
        <LeftModalHeader
          title={translate('policy.personal')}
          hideSeparator={true}
          rightComponent={
            <SHeaderWrapper>
              <Button
                source={IC_SEARCH}
                onPress={() => navigateToSearchScreen()}
              />
            </SHeaderWrapper>
          }
        />
        <SelectedHeader title={translate('policy.personal')} />
        <_Comp
          params={params}
          renderItem={renderItem}
          onFilterPress={onFilterPress}
        />
      </ScreenWrapper>
    </SelectedItemProvider>
  );
});

const _Comp = memo(
  ({
    renderItem,
    params,
    onFilterPress,
  }: {
    renderItem: any;
    params: any;
    onFilterPress: any;
  }) => {
    const {selectedState, selectedIds} = useSelectedItem();
    const scrollEnabled = useDerivedValue(() => selectedIds.value.length == 0);

    return (
      <ScrollableTabView.Provider
        disabledLoadData={selectedState}
        scrollEnabled={scrollEnabled}
        renderItem={renderItem}
        getItemByQuery={getPoliciesByQuery}
        useItemByQuery={usePoliciesByQuery}
        tabs={[
          {
            title: translate('policy.favorites'),
            content: p => (
              <ScrollableTabView.List
                index={0}
                query={'mine/fav'}
                requestFn={requestPolicyByMineFav}
              />
            ),
          },
          {
            title: translate('policy.sent_by_me'),
            content: p => (
              <>
                <FilterContainer params={params} onPress={onFilterPress} />
                <ScrollableTabView.List
                  index={1}
                  query={'mine'}
                  params={params}
                  requestFn={requestPolicyByMine}
                />
              </>
            ),
          },
        ]}>
        <ScrollableTabView.TabBar />
        <ScrollableTabView.Content />
      </ScrollableTabView.Provider>
    );
  },
);

const SHeaderWrapper = styled.View`
  margin-right: 16px;
`;

const FilterContainer = memo(
  ({params, onPress}: {params: any; onPress: () => void}) => {
    const count = useMemo(() => getCountFilter(params), [params]);

    return (
      <SFilterContainer onPress={onPress}>
        <SFilterTitle>{translate('policy.list_of_document')}</SFilterTitle>
        <SFilterValue>
          {translate('policy.filter')}{' '}
          {count ? <SNumber>({count})</SNumber> : null}
        </SFilterValue>
      </SFilterContainer>
    );
  },
);

const SFilterContainer = styled.TouchableOpacity`
  flex-direction: row;
  justify-content: space-between;
  padding: 12px 16px;
  align-items: center;
`;
const SFilterTitle = styled.Text`
  font-size: 16px;
  line-height: 24px;
  color: ${p => p.theme.grey1};
  font-family: ${Fonts.Bold};
`;
const SFilterValue = styled.Text`
  color: ${p => p.theme.grey2};
  font-size: 12px;
  line-height: 16px;
`;
const SNumber = styled.Text`
  color: ${Colors.red1};
  font-size: 12px;
  line-height: 16px;
`;
