import React, {memo} from 'react';
import {styled} from '@true/core';
import {TouchableOpacityProps} from 'react-native';

export const MenuItem = memo(
  ({
    source,
    title,
    ...rest
  }: {source?: any; title: string} & TouchableOpacityProps) => {
    return (
      <SMenuContainer {...rest}>
        <SMenuWrapper>
          {source ? <SMenuIcon source={source} /> : null}
          <SMenuTitle>{title}</SMenuTitle>
        </SMenuWrapper>
        <SMenuDivider />
      </SMenuContainer>
    );
  },
);

const SMenuContainer = styled.TouchableOpacity``;

const SMenuWrapper = styled.View`
  flex-direction: row;
  align-items: center;
  padding: 16px;
`;
const SMenuIcon = styled.Image``;

const SMenuTitle = styled.Text`
  color: ${p => p.theme.grey1};
`;
const SMenuDivider = styled.View`
  height: 1px;
  background-color: ${p => p.theme.grey5};
  margin: 0 16px;
`;
