import React, {memo} from 'react';
import {HTMLRenderer, MyButton} from '@policy/components';
import {StyleSheet, View} from 'react-native';
import {translate, useTheme} from '@true/core';
import {useBoolean} from '@true/core';
import {Fonts} from '@true/core';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';

interface Props {
  policy?: IDetailPolicy;
}

const slop = (num: number) => ({
  top: num,
  bottom: num,
  left: num,
  right: num,
});

export const DetailPolicyOverview = memo((props: Props) => {
  const theme = useTheme();

  const [htmlExtended, htmlShow, htmlHide, htmlToggle] = useBoolean();
  return (
    <>
      <HTMLRenderer
        htmlContent={props.policy?.content || ''}
        containerStyle={[
          styles.html,
          {
            overflow: 'hidden',
            backgroundColor: theme.grey6,
            borderColor: theme.grey4,
            maxHeight: !htmlExtended ? 135 : undefined,
          },
        ]}
        defaultTextProps={{
          style: {
            fontSize: 14,
            lineHeight: 20,
            color: theme.grey2,
          },
        }}
        canEdit={false}
      />
      {/*<View style={{alignItems: 'flex-start'}}>*/}
      <MyButton
        hitSlop={slop(8)}
        title={translate('policy.see_details')}
        disableDebounce={true}
        containerStyle={styles.myButtonContainer}
        textStyle={[
          styles.myButtonText,
          {
            color: theme.primaryColor,
          },
        ]}
        onPress={htmlToggle}
      />
      {/*</View>*/}
    </>
  );
});

const styles = StyleSheet.create({
  html: {
    borderLeftWidth: 4,
    padding: 12,
    borderRadius: 4,
  },
  myButtonContainer: {
    justifyContent: 'flex-start',
    minHeight: undefined,
    marginLeft: 0,
    marginTop: 8,
  },
  myButtonText: {
    paddingLeft: 0,
    paddingRight: 0,
    fontSize: 14,
    lineHeight: 24,
    fontFamily: Fonts.Medium,
  },
});
