import React, {memo, useCallback, useState} from 'react';
import {
  DynamicHeader,
  FormScroll,
  MyTextInput,
  ScreenWrapper,
  MySelectorInput,
  BottomButton,
  MyKeyboardAvoidingView,
} from '@true/ui';
import {styled, translate} from '@true/core';
import {useNavigationParams} from '@true/core';
import {useMasterVersionDoctypes} from '@policy/store/masterVersionDoctypes';
import {getCollections, useCollectionsByQuery} from '@policy/store/collections';
import {getDoctypes, useDoctypesByQuery} from '@policy/store/doctypes';

export interface EditMasterVersionProps {
  id: number;
}

export const EditMasterVersion = memo(() => {
  const {id} = useNavigationParams<EditMasterVersionProps>();

  const masterVersion = useMasterVersionDoctypes(id);

  const [params, setParams] = useState({
    name: masterVersion?.name || '',
    collection_id: masterVersion?.collection_id,
    doctype_id: masterVersion?.doctype_id,
  });

  const collections = useCollectionsByQuery('all').map(_id => {
    const _item = getCollections(_id);
    return {
      label: _item?.name,
      value: _id,
    };
  }) as any;

  const doctypes = useDoctypesByQuery('all').map(_id => {
    const _item = getDoctypes(_id);
    return {
      label: _item?.name,
      value: _id,
    };
  }) as any;

  const onChangeParam = useCallback((keyName: string, value: any) => {
    setParams(prev => ({
      ...prev,
      [keyName]: value,
    }));
  }, []);

  const onSubmit = useCallback(() => {}, [params]);

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.edit_master_version_info')} />
      <MyKeyboardAvoidingView>
        <FormScroll>
          <SWrapper>
            <MyTextInput
              value={params.name}
              title={translate('policy.master_version_name')}
              required={true}
              keyName={'name'}
              onTextChange={onChangeParam}
            />
          </SWrapper>
          <SWrapper>
            <MySelectorInput
              title={'Collection'}
              required={true}
              value={params.collection_id}
              keyName={'collection_id'}
              onValueChange={onChangeParam}
              options={collections}
            />
          </SWrapper>
          <SWrapper>
            <MySelectorInput
              title={'Document type'}
              required={true}
              value={params.doctype_id}
              keyName={'doctype_id'}
              onValueChange={onChangeParam}
              options={doctypes}
            />
          </SWrapper>
        </FormScroll>
        <SButtonWrapper>
          <BottomButton onRightPress={onSubmit} />
        </SButtonWrapper>
      </MyKeyboardAvoidingView>
    </ScreenWrapper>
  );
});

const SWrapper = styled.View`
  margin: 16px 16px 0;
`;

const SButtonWrapper = styled.View`
  margin: 16px 16px 0;
`;
