import React, {memo, useState} from 'react';
import {DynamicHeader, LeftModalHeader, ScreenWrapper} from '@true/ui';
import {translate} from '@true/core';
import {useNavigationParams} from '@true/core';
import {
  defaultHomeFilterParams,
  IHomeFilterParams,
} from '@policy/screens/HomeScreen/index';
import {FilterComp} from '@policy/components/FilterComp';

export interface HomeFilterProps {
  params: IHomeFilterParams;
  setParams: (value: any) => void;
}

export const HomeFilter = memo(() => {
  const {params, setParams} = useNavigationParams<HomeFilterProps>();

  const [_params, _setParams] = useState(params);

  console.log(_params);

  return (
    <ScreenWrapper>
      <DynamicHeader title={translate('policy.filter')} />
      <FilterComp
        items={[
          'collection',
          'folder',
          'storage',
          'receiver',
          'doctype',
          'filter',
          'created_date',
          'effective_date',
          'expired_date',
        ]}
        params={_params}
        setParams={_setParams}
        defaultParams={defaultHomeFilterParams}
        onSubmit={setParams}
      />
    </ScreenWrapper>
  );
});
