import {styled} from '@true/core';
import {memo, useCallback, useState} from 'react';
import {ICollection} from '@policy/store/collections/types';
import {Text} from 'react-native';
import {IC_FOLDER, IC_OUTLINE_DROPDOWN} from '@policy/assets';
import FastImage from 'react-native-fast-image';
import {Fonts} from '@true/core';

export const CollectionSection = memo(
  ({
    item,
    index,
    onToggle,
  }: {
    index?: number;
    item: ICollection;
    onToggle?: () => void;
  }) => {
    const [collapse, setCollapse] = useState(false);
    const _onToggle = useCallback(() => {
      setCollapse(p => !p);
      onToggle?.();
    }, [onToggle]);

    return (
      <SContainer onPress={_onToggle}>
        <SIcWrapper>
          <SIc source={IC_FOLDER} />
        </SIcWrapper>
        <STextWrapper>
          <SText>{item.name}</SText>
        </STextWrapper>
        <SIcWrapper>
          <SRightIc
            style={{transform: [{rotate: collapse ? '-90deg' : '0deg'}]}}
            source={IC_OUTLINE_DROPDOWN}
          />
        </SIcWrapper>
      </SContainer>
    );
  },
);

const SContainer = styled.TouchableOpacity`
  padding: 12px 12px;
  background-color: ${p => p.theme.grey5};
  flex-direction: row;
  margin-top: 8px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
`;

const SIcWrapper = styled.View`
  align-items: center;
  justify-content: center;
  max-height: 48px;
  margin-right: 8px;
`;
const SIc = styled(FastImage)`
  height: 24px;
  width: 24px;
`;

const SRightIc = styled(FastImage)`
  height: 24px;
  width: 24px;
`;

const STextWrapper = styled.View`
  flex: 1;
  min-height: 24px;
  justify-content: center;
`;

const SText = styled.Text`
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Medium};
`;
