import { Core, Fetch } from "@true/core";
import { batch } from "react-redux";
import { IDetailPolicy } from "@policy/store/detailPolicy/types";
import { resetDetailPolicy, syncDetailPolicy } from "@policy/store/detailPolicy";
import { syncPolicies } from "@policy/store/policies";

interface IParams {
  id: number | string;
}

export const requestDetailPolicy = async (params: IParams) => {
  const {data} = await Fetch.postWithToken<{
    doc: IDetailPolicy
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/doc/detail`, params);
  batch(() => {
    if (data.doc) {
      syncPolicies([data.doc]);
    }
  })

  return data.doc
};
