import React, { memo, useCallback, useEffect, useMemo, useState } from "react";
import { RecycleList } from "@true/ui";
import { useAsyncFn } from "react-use";
import { PoliciesItem } from "@policy/screens/HomeScreen/PoliciesItem";
import { getPoliciesByQuery } from "@policy/store/policies";
import { ActivityIndicator, Image, View } from "react-native";
import { styled, translate } from "@true/core";
import { useAutoToastError } from "@true/core";
import { IC_EMPTY_LIST } from "@true/core";

export const PolicyList = memo(
  ({
    params,
    query,
    requestFn,
   ListHeaderComponent
  }: {
    params: any;
    query: string;
    requestFn: (params: any) => Promise<any>;
    ListHeaderComponent?: JSX.Element;
  }) => {
    const [{loading: firstLoading, error: firstLoadError}, onFirstLoad] =
      useAsyncFn(requestFn);
    const [{loading: refreshing, error: refreshingError}, onRefresh] =
      useAsyncFn(requestFn);
    const [
      {loading: paramChangeLoading, error: paramsChangeError},
      onParamsChange,
    ] = useAsyncFn(requestFn);

    const renderItem = useCallback((data: number, index?: number) => {
      return <PoliciesItem id={data} index={index} />;
    }, []);

    const [itemList, setItemList] = useState<(number | string)[]>([]);
    const [mounted, setMounted] = useState(false);

    const submitParams = useMemo(() => {
      let result: any = {};

      if (params) {
        for (let key in params) {
          if (params[key]) result[key] = params[key];
        }
      }

      return result;
    }, [params]);

    useEffect(() => {
      onFirstLoad(submitParams).then(() => {
        setItemList(getPoliciesByQuery(query));
        setMounted(true);
      });
    }, [query]);

    useEffect(() => {
      if (mounted)
        onParamsChange(submitParams).then(() => {
          setItemList(getPoliciesByQuery(query));
        });
    }, [query, submitParams]);

    const onLoadData = useCallback(
      (isRefreshing: boolean, isLoadMore: boolean) => {
        if (isRefreshing) {
          onRefresh(params).then(() => {
            setItemList(getPoliciesByQuery(query));
          });
        }
      },
      [submitParams],
    );

    useAutoToastError(firstLoadError);
    useAutoToastError(refreshingError);
    useAutoToastError(paramsChangeError);

    return mounted ? (
      <RecycleList
        ListHeaderComponent={ListHeaderComponent}
        data={itemList}
        itemHeight={150}
        renderItem={renderItem}
        loading={firstLoading}
        isRefreshing={refreshing}
        renderEmptyView={() => (
          <View
            style={{
              alignItems: 'center',
              justifyContent: 'center',
              flex: 1,
            }}>
            <Image source={IC_EMPTY_LIST} />
            <SText style={{paddingTop: 16}}>
              {translate('policy.no_more_doc')}
            </SText>
          </View>
        )}
        isLoadMore={false}
        error={false}
        refreshable={true}
        onLoadData={onLoadData}
        loadMorable={false}
      />
    ) : (
      <ActivityIndicator style={{top: 50}} />
    );
  },
);

const SDivider = styled.View`
  height: 8px;
  background-color: ${p => p.theme.grey4};
`;

const SText = styled.Text`
  color: ${p => p.theme.grey1};
`;
