import {styled} from '@true/core';
import React, {memo} from 'react';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import FastImage from 'react-native-fast-image';
import {TextAvatar} from '@policy/components/TextAvatar';
import {useUserGroup} from '@true/core';
import {firstChar} from '@policy/utils';
import {Fonts} from '@true/core';
import {stripSpecialCharacters} from '@true/core';
import { MapList } from '@policy/components/MapList';

export const DetailRecipient = memo(({policy}: {policy?: IDetailPolicy}) => {
  return (
    <SContainer>
      <MapList
        data={policy?.teams || []}
        renderItem={({item}) => <RecipientItem id={item} />}
      />
    </SContainer>
  );
});

const SContainer = styled.View`
  margin-bottom: 8px;
`;

const RecipientItem = memo(({id}: {id: string | number}) => {
  const team = useUserGroup(id);
  return (
    <SRecipientContainer>
      <TextAvatar size={40} text={team?.name || ''} />
      <STextWrapper>
        <STitle>{stripSpecialCharacters(team?.name)}</STitle>
        <SSubTitleWr>
          @{team?.path} . {team?.owners?.length || 0} members
        </SSubTitleWr>
      </STextWrapper>
    </SRecipientContainer>
  );
});

const SRecipientContainer = styled.View`
  flex-direction: row;
  align-items: center;
  margin: 8px 0;
`;
const STextWrapper = styled.View`
  margin-left: 8px;
`;
const STitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.grey1};
  font-family: ${Fonts.Medium};
`;
const SSubTitleWr = styled.Text`
  font-size: 12px;
  line-height: 16px;
  color: ${p => p.theme.grey2};
`;
