import React, { memo, RefObject, useCallback, useMemo, useRef } from "react";
import {
  FormScroll,
  MyDateTimeInput,
  MySelectorInput,
  MyUserPickerInput,
  UISelectorInput,
  UIInput
} from '@true/ui';
import {styled, translate, useTheme} from '@true/core';
import {
  getCollections,
  useCollections,
  useCollectionsByQuery,
} from '@policy/store/collections';
import {getStorages, useStoragesByQuery} from '@policy/store/storages';
import {getDoctypes, useDoctypesByQuery} from '@policy/store/doctypes';
import {getUserGroup, useUserGroupByQuery} from '@true/core';
import {BottomButton} from '@true/ui';
import {goBack} from '@true/core';
import {Fonts} from '@true/core';
import {AnimatedBottomSpace} from '../AnimatedBottomSpace';
import { ScrollView, View } from "react-native";

const filterOpts = [
  {label: translate('policy.bookmarked'), value: 'bookmarked'},
  {label: translate('policy.created'), value: 'created'},
  {label: translate('policy.reviewed'), value: 'reviewed'},
  {label: translate('policy.published'), value: 'published'},
  {label: translate('policy.pending'), value: 'pending'},
  {label: translate('policy.rejected'), value: 'rejected'},
];
const sortOpts = [
  {label: translate('policy.issued'), value: 'issued'},
  {label: translate('policy.effective'), value: 'effective'},
];

export type IInputFilterType =
  | 'collection'
  | 'storage'
  | 'receiver'
  | 'doctype'
  | 'filter'
  | 'sort'
  | 'created_by'
  | 'folder'
  | 'created_date'
  | 'effective_date'
  | 'expired_date';

interface Props {
  items: IInputFilterType[];
  params: any;
  setParams: (params: any) => any;
  defaultParams: any;
  onSubmit: (params: any) => any;
}

export const FilterComp = memo((props: Props) => {
  const {items, params, setParams, defaultParams, onSubmit} = props;

  const ref = useRef<{ [id: string]: UISelectorInput }>({})

  const _onSubmit = useCallback(() => {
    onSubmit(params);
    goBack();
  }, [params, onSubmit]);

  const onReset = useCallback(() => {
    onSubmit(defaultParams);
    goBack();
  }, [onSubmit]);

  const theme = useTheme();

  return (
    <>
      <FormScroll>
        <ListItems itemsRef={ref} params={params} setParams={setParams} items={items}/>
        {/*{items.map(itemType => (*/}
        {/*  <SItemContainer>*/}
        {/*    <ItemInput type={itemType} params={params} setParams={setParams} />*/}
        {/*  </SItemContainer>*/}
        {/*))}*/}
      </FormScroll>
      <BottomButton
        containerStyle={{marginHorizontal: 16, marginVertical: 12}}
        leftBgColor={theme.primaryColor + '20'}
        textColor={theme.primaryColor}
        leftText={translate('policy.reset')}
        rightText={translate('policy.update')}
        onRightPress={_onSubmit}
        onLeftPress={onReset}
      />
      <AnimatedBottomSpace />
    </>
  );
});

const ListItems = memo(({
  items,
  params,
  setParams,
  itemsRef
}: {
  items: IInputFilterType[],
  params: any,
  setParams: any;
  itemsRef: RefObject<{ [id: string]: UISelectorInput|null }>;
}) => {

  return items.map(item => <SItemContainer>
    <ItemInput itemsRef={itemsRef} type={item} params={params} setParams={setParams} />
  </SItemContainer>)
})

const ItemInput = memo(
  ({
    type,
    params,
    setParams,
    itemsRef
  }: {
    type: IInputFilterType;
    params: any;
    setParams: any;
    itemsRef: RefObject<{ [id: string]: UISelectorInput|null }>;
  }) => {

    const title = useMemo(() => {
      if (type == 'receiver') return ['recipient'];
      if (type == 'doctype') return ['document_type'];
      if (type.indexOf('date') !== -1) return ['from_date', 'to_date'];
      return [type];
    }, [type]);

    const onChangeParams = useCallback((key: string, val: any) => {
      // if (key.indexOf('date') !== -1) {
      //   setParams((prev: any) => ({
      //     ...prev,
      //     [key]: val[0],
      //   }));
      //   return;
      // }

      setParams((prev: any) => ({
        ...prev,
        [key]: val,
      }));

      if (key == 'collection') {
        itemsRef.current?.['folder']?.onClearValue()
        setParams((prev: any) => ({
          ...prev,
          folder: '',
        }));
      }
    }, [itemsRef]);

    /** --------------------------------------------------
     * Date input
     */
    if (type.indexOf('date') !== -1) {
      return (
        <>
          <SDateTitle>{translate('policy.' + type)}</SDateTitle>
          <SRow>
            <UIInput.DateTime
              style={{flex: 1}}
              mode={'date'}
              title={translate('policy.' + title[0])}
              value={params[type + '_from']}
              keyName={type + '_from'}
              onChangeValue={onChangeParams}
            />
            <UIInput.DateTime
              style={{flex: 1, marginLeft: 16}}
              mode={'date'}
              title={translate('policy.' + title[1])}
              value={params[type + '_to']}
              keyName={type + '_to'}
              onChangeValue={onChangeParams}
            />
          </SRow>
        </>
      );
    }

    /** ---------------------------------------------
     *  User Input
     */
    if (type == 'created_by') {
      return (
        <UIInput.UserPicker
          title={translate('policy.' + title[0])}
          value={params[type]}
          keyName={type}
          onChangeValue={onChangeParams}
          type={"user"}
        />
      );
    }

    /** --------------------------------------------------
     * Selector Input
     */
    const storageOpts = useStoragesByQuery('all').map(id => ({
      label: getStorages(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    const collectionOpts = useCollectionsByQuery('all').map(id => ({
      label: getCollections(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    const receiverOpts = useUserGroupByQuery('teams').map(id => ({
      label: getUserGroup(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    const doctypeOpts = useDoctypesByQuery('all').map(id => ({
      label: getDoctypes(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    const folderOpts =
      useCollections(params?.collection || '')?.folders?.map(item => ({
        label: item?.name || translate('policy.undefined'),
        value: item?.id,
      })) || [];

    const options = useMemo(() => {
      if (type == 'collection') return collectionOpts;
      if (type == 'storage') return storageOpts;
      if (type == 'receiver') return receiverOpts;
      if (type == 'doctype') return doctypeOpts;
      if (type == 'folder') return folderOpts;
      if (type == 'sort') return sortOpts;
      if (type == 'filter') return filterOpts;
      return [];
    }, [type, params?.collection]) as any;

    return (
      <UIInput.Selector
        ref={view => {
          if (itemsRef.current) itemsRef.current[type] = view
        }}
        title={translate('policy.' + title[0])}
        value={params[type]}
        keyName={type}
        onChangeValue={onChangeParams}
        options={options}
      />
    );
  },
);

const SItemContainer = styled.View`
  margin: 16px 16px 0 16px;
`;

const SRow = styled.View`
  flex-direction: row;
`;
const SDateTitle = styled.Text`
  font-size: 14px;
  line-height: 22px;
  font-family: ${Fonts.Bold};
  margin-bottom: 4px;
  color: ${p => p.theme.grey2};
`;
