import React, {memo, useCallback, useState} from 'react';
import {memoForwardRef} from '@true/core';
import {
  DynamicHeader,
  LeftModalHeader,
  ScreenWrapper,
  ScrollableTabView,
} from '@true/ui';
import {styled, translate} from '@true/core';
import {IC_SEARCH} from '@true/core';
import {
  SelectedItemProvider,
  useSelectedItem,
} from '@policy/context/SelectedItemContext';
import {SelectedHeader} from '@policy/screens/HomeScreen/SelectedHeader';
import {
  navigateToHomeFilter,
  navigateToSearchScreen,
  navigateToWebViewScreen,
} from '@policy/utils';
import {PoliciesItem} from '@policy/screens/HomeScreen/PoliciesItem';
import {requestPolicy} from '@policy/store/policies/functions';
import {getPoliciesByQuery, usePoliciesByQuery} from '@policy/store/policies';
import {FilterIcon} from '@policy/components/FilterIcon';
import {logout} from '@true/core';
import {Button} from '@policy/components';
import {Core} from '@true/core';
import {useDerivedValue} from 'react-native-reanimated';

export interface IHomeFilterParams {
  collection?: number;
  storage?: number;
  receiver?: number;
  doctype?: number;
  sort?: 'issued' | 'effective';
  filter?:
    | 'bookmarked'
    | 'created'
    | 'reviewed'
    | 'published'
    | 'pending'
    | 'rejected';
}

export const defaultHomeFilterParams: IHomeFilterParams = {};

export const HomeScreen = memoForwardRef(() => {
  const [params, setParams] = useState(defaultHomeFilterParams);

  const rightCompRenderer = useCallback(
    () => (
      <SRow>
        <SHeaderBtn onPress={navigateToSearchScreen}>
          <SHeaderIc source={IC_SEARCH} />
        </SHeaderBtn>
        <FilterIcon
          style={{marginRight: 16}}
          params={params}
          onPress={() => navigateToHomeFilter({params, setParams})}
        />
      </SRow>
    ),
    [params],
  );

  const renderItem = useCallback(
    (policyId: number, index?: number, isLast?: boolean) => {
      return <PoliciesItem id={policyId} isLast={isLast} />;
    },
    [],
  );

  console.log('clientKey', Core.clientKey);
  console.log('accessToken', Core.accessToken);
  // return null;
  return (
    <SelectedItemProvider>
      <ScreenWrapper isGrey={true}>
        <LeftModalHeader
          hideSeparator={true}
          title={translate('policy.home')}
          rightComponent={rightCompRenderer()}
        />
        <SelectedHeader title={translate('policy.home')} />
        {/*<Button title={'logout'} onPress={logout} />*/}
        {/*<Button*/}
        {/*  title={'WebView'}*/}
        {/*  onPress={() =>*/}
        {/*    navigateToWebViewScreen({*/}
        {/*      uri: 'base-space://update/411',*/}
        {/*    })*/}
        {/*  }*/}
        {/*/>*/}
        <_ScrollTabView
          params={params}
          setParams={setParams}
          renderItem={renderItem}
        />
      </ScreenWrapper>
    </SelectedItemProvider>
  );
});

const _ScrollTabView = memo(
  (props: {params: IHomeFilterParams; setParams: any; renderItem: any}) => {
    const {selectedState, selectedIds} = useSelectedItem();

    const scrollEnabled = useDerivedValue(() => selectedIds.value.length == 0);
    const disabledLoadData = useDerivedValue(
      () => selectedIds.value.length > 0,
    );

    return (
      <ScrollableTabView.Provider
        {...props}
        requestFn={requestPolicy}
        getItemByQuery={getPoliciesByQuery}
        useItemByQuery={usePoliciesByQuery}
        scrollEnabled={scrollEnabled}
        disabledLoadData={selectedState}
        tabs={[
          {
            title: translate('policy.all_policies'),
            content: p => <ScrollableTabView.List index={p.index} />,
          },
          {
            title: translate('policy.pending_approval'),
            content: p => (
              <ScrollableTabView.List index={p.index} tab={'pending'} />
            ),
          },
          {
            title: translate('policy.need_attestation'),
            content: p => (
              <ScrollableTabView.List index={p.index} tab={'attesting'} />
            ),
          },
          {
            title: translate('policy.followings'),
            content: p => (
              <ScrollableTabView.List index={p.index} tab={'following'} />
            ),
          },
        ]}>
        <ScrollableTabView.TabBar />
        <ScrollableTabView.Content />
      </ScrollableTabView.Provider>
    );
  },
);

const SRow = styled.View`
  flex-direction: row;
`;

const SHeaderBtn = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    left: 8,
    bottom: 8,
    right: 8,
  },
}))`
  margin-right: 16px;
`;

const SHeaderIc = styled.Image`
  width: 24px;
  height: 24px;
`;
