import React, {memo, useMemo} from 'react';
import {styled, translate} from '@true/core';
import {getStatus} from '@policy/components/PolicyStatuses';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {Fonts} from '@true/core';
import {UserAvatar} from '@policy/components';
import {
  IM_EFFECTIVE_BADGE,
  IM_PENDING_BADGE,
  IM_REJECTED_BADGE,
} from '@policy/assets';
import {useUser} from '@true/core';
import {SubInfoComp} from '@policy/screens/HomeScreen/PoliciesItem';
import {getDoctypes} from '@policy/store/doctypes';
import {SCodeComp} from '@policy/components/SCodeComp';
import {StyleSheet, View} from 'react-native';

export const DetailHeader = memo(
  ({policy, show}: {policy?: IDetailPolicy; show: () => void}) => {
    const creator = useUser(policy?.user_id || '');

    const statusBadge = useMemo(() => {
      if (policy?.status == 1) return IM_EFFECTIVE_BADGE;
      if (policy?.status == 0) return IM_PENDING_BADGE;
      if (policy?.status == -1) return IM_REJECTED_BADGE;
      return null;
    }, [policy?.status]);

    return (
      <SContainer>
        <STitleWrapper>
          {/*<PolicyStatuses policy={policy} />*/}
          <SCodeComp policy={policy} />
          <STitle>{policy?.name}</STitle>
        </STitleWrapper>
        <SHeaderRowWrapper>
          <SHeaderColWrapper>
            <SHeaderInfoRow>
              <SubInfoComp
                fontSize={14}
                style={styles.leftSubInfo}
                label={translate('policy.issued_date')}
                value={policy?.date?.released_date}
                isDate={true}
              />
              <SRowDivider />
              <SubInfoComp
                fontSize={14}
                style={styles.rightSubInfo}
                label={translate('policy.effective_date')}
                value={policy?.date?.effective_date}
                isDate={true}
              />
            </SHeaderInfoRow>
            <SubInfoComp
              style={styles.subInfo}
              fontSize={14}
              label={translate('policy.expired_date')}
              value={policy?.date?.expired_date}
              isDate={true}
            />
            <SubInfoComp
              fontSize={14}
              style={styles.subInfo}
              label={translate('policy.published_by')}
              value={creator?.name}
              onPress={show}
            />
            <SRow style={styles.subInfo}>
              <View style={{flex: 1}}>
                <SubInfoComp
                  fontSize={14}
                  label={translate('policy.status')}
                  value={getStatus(policy).text}
                  color={getStatus(policy).color}
                />
                <SubInfoComp
                  fontSize={14}
                  style={styles.subInfo}
                  label={translate('policy.type')}
                  value={getDoctypes(policy?.doctype_id || '')?.name}
                />
              </View>
              <SEffectiveIc source={statusBadge} />
            </SRow>
          </SHeaderColWrapper>
        </SHeaderRowWrapper>
      </SContainer>
    );
  },
);

const styles = StyleSheet.create({
  subInfo: {
    marginTop: 8,
  },
  leftSubInfo: {
    paddingRight: 8,
    marginTop: 8,
  },
  rightSubInfo: {
    marginTop: 8,
  },
});

const SContainer = styled.View`
  background-color: ${p => p.theme.backgroundColor};
`;

const STitleWrapper = styled.View`
  flex: 1;
  padding: 0 16px;
`;
const SEffectiveWrapper = styled.View`
  margin-right: 16px;
  margin-top: 12px;
`;
const SHeaderRowWrapper = styled.View`
  flex-direction: row;
`;
const SHeaderColWrapper = styled.View`
  padding: 0 16px;
  flex: 1;
`;

const STitle = styled.Text`
  font-size: 20px;
  line-height: 24px;
  color: ${p => p.theme.grey1};
  font-family: ${Fonts.Bold};
  margin-top: 8px;
`;

const SEffectiveIc = styled.Image`
  height: 44px;
  width: 52px;
`;

const SHeaderInfoRow = styled.View`
  flex-direction: row;
  flex-wrap: wrap;
`;

const SHeaderInfoIc = styled.Image`
  margin-right: 8px;
`;

const SHeaderInfoSection = styled.View`
  justify-content: center;
`;

const SHeaderInfoTitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.grey2};
  margin-right: 10px;
`;

const SHeaderInfoDesc = styled.Text`
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Medium};
`;

const SUserWrapper = styled.TouchableOpacity`
  flex-direction: row;
  align-items: center;
`;

const SAvatar = styled(UserAvatar)`
  margin: 0 4px 0 0;
`;

const SRow = styled.View`
  flex-direction: row;
`;

const SRowDivider = styled.View`
  margin-top: 12px;
  height: 8px;
  margin-right: 8px;
  background-color: ${p => p.theme.grey5};
  width: 1px;
`;
