import React, { memo, useCallback, useMemo } from "react";
import { LeftModalHeader, RecycleList, ScreenWrapper } from "@true/ui";
import { styled, translate } from "@true/core";
import { navigateToSearchScreen } from "@policy/utils";
import { IC_SEARCH } from "@true/core";
import { getCollections, useCollectionsByQuery } from "@policy/store/collections";
import { CollectionSection } from "@policy/screens/CollectionScreen/CollectionSection";
import { ICollection, IFolder } from "@policy/store/collections/types";
import { CollectionItem } from "@policy/screens/CollectionScreen/CollectionItem";

export const CollectionScreen = memo(() => {
  const collections = useCollectionsByQuery('all').map(id => {
    const _collect = getCollections(id) as ICollection;
    return _collect;
    // return {
    //   item: _collect,
    //   data: _collect?.folders || [],
    // };
  });

  const map = useMemo(() => {
    let result: Record<string, IFolder[]> = {};
    collections.forEach(item => {
      result[item.id] = item.folders;
    });
    return result;
  }, [collections]);
  // const data = co;

  const renderSection = useCallback(
    (
      // data: {item: ICollection; data: IFolder[]},
      data: ICollection,
      onToggle?: () => void,
      index?: number,
    ) => {
      return (
        <CollectionSection index={index} item={data} onToggle={onToggle} />
      );
    },
    [],
  );

  const renderItem = useCallback((data: IFolder, index?: number) => {
    return <CollectionItem index={index} item={data} />;
  }, []);

  return (
    <ScreenWrapper>
      {/* Header -----------------*/}
      <LeftModalHeader
        title={translate('policy.collection')}
        rightComponent={
          <SHeaderBtn onPress={navigateToSearchScreen}>
            <SHeaderIc source={IC_SEARCH} />
          </SHeaderBtn>
        }
      />
      {/* Content -----------------*/}
      <SListWrapper>
        <RecycleList
          map={map}
          mapKey={'id'}
          data={collections}
          itemHeight={40}
          renderItem={renderItem}
          renderSection={renderSection}
          loading={false}
          isRefreshing={false}
          isLoadMore={false}
          error={false}
          refreshable={false}
          loadMorable={false}
        />
      </SListWrapper>
    </ScreenWrapper>
  );
});

const SListWrapper = styled.View`
  margin: 0 16px;
  flex: 1;
`;

const SHeaderBtn = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    left: 8,
    bottom: 8,
    right: 8,
  },
}))`
  margin-right: 16px;
`;

const SHeaderIc = styled.Image`
  width: 24px;
  height: 24px;
`;
